import React from 'react'
import { MAPBOX_API_KEY } from '../routes/About'
import Map from 'react-map-gl'
import SwipeableEdgeDrawer from '../component/SwipeableDrawer'
import Jam from '../component/Jam'
import DataNavbar from '../component/DataNavbar'
import AccountDrawer from '../component/AccountDrawer'


const LayoutKasar = () => {
    return (
        <div style={{display: "block"}}>
            <AccountDrawer></AccountDrawer>
            <Jam></Jam>
            <Map
                initialViewState={{
                    latitude: -6.903363,
                    longitude: 107.608138,
                    zoom: 12,
                    attributionControl: false
                }}
                style={{ width: "100vw", height: "100vh", position: "fixed"}}
                mapStyle="mapbox://styles/mapbox/dark-v10"
                mapboxAccessToken={MAPBOX_API_KEY}>
            </Map>
            <DataNavbar></DataNavbar>
            <SwipeableEdgeDrawer></SwipeableEdgeDrawer>
        </div>

    )
}

export default LayoutKasar